
#resumeEditor {
  display: -ms-flexbox;
  display: flex;
  min-width: 35%;
}
#resumeEditor .UIColumn {
  color: #fff;
  width: 20%;
  background: #1f2126;
}
#resumeEditor .UIColumn .active {
  background: #fff;
  color: #1f2126;
}
#resumeEditor .UIColumn nav {
  margin-top: 12px;
}
#resumeEditor .UIColumn nav li:hover {
  background: #fff;
}
#resumeEditor .UIColumn nav li:hover .icon {
  color: #1f2126;
}
#resumeEditor .UIColumn nav li {
  padding: 16px 0;
}
#resumeEditor .UIColumn nav li .icon {
  display: block;
  width: 26px;
  height: 26px;
  margin: 0 auto;
  font-size: 28px;
}
#resumeEditor .inputColumn {
  overflow-y: scroll;
  width: 80%;
  padding: 8px 23px;
  font-size: 20px;
  color: #666;
}
#resumeEditor .inputColumn .form-group {
  margin-top: 16px;
}
#resumeEditor .inputColumn .form-group input {
  display: block;
  width: 100%;
  margin-top: 16px;
  padding: 5px 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  outline: 0;
}
#resumeEditor .inputColumn .form-group input:focus {
  border: 1px solid #666;
  box-shadow: 0 0 1px 1px #666;
}
#resumeEditor .inputColumn .subItem hr {
  margin: 20px auto;
}
#resumeEditor .inputColumn .option-btn {
  display: -ms-flexbox;
  display: flex;
}
#resumeEditor .inputColumn .option-btn .btn {
  display: inline-block;
  font-size: 20px;
  text-align: center;
  line-height: 32px;
  width: 72px;
  height: 32px;
  margin: 0 auto;
  border-radius: 5px;
  cursor: pointer;
  color: #fff;
  background: #1f2126;
}
#resumeEditor .inputColumn .option-btn .btn:hover {
  color: #1f2126;
  background: #fff;
  border: 2px solid #1f2126;
}



