
#resumePreview {
  overflow-y: scroll;
  overflow-x: hidden;
}
#resumePreview .personal-info {
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  background: #333;
  color: #fff;
}
#resumePreview .personal-info .main-wraps {
  margin-left: 10%;
  min-height: 150px;
}
#resumePreview .personal-info .side-wraps {
  margin-top: 2.5%;
  margin-left: 10%;
  letter-spacing: .3em;
  line-height: 1.5em;
}
#resumePreview .item {
  position: relative;
  border-top: 2px solid #ccc;
  border-bottom: 2px solid #ccc;
  margin: 30px 0;
  width: 100%;
}
#resumePreview .item h3 {
  color: #333;
  position: absolute;
  margin-top: -10px;
  left: 10%;
  background: #fff;
}
#resumePreview .item ol {
  width: 100%;
}
#resumePreview .item ol li {
  width: 100%;
  margin: 10px 0;
}
#resumePreview .item ol li .title {
  display: inline-block;
  margin-left: 8%;
  width: 15%;
}
#resumePreview .item ol li .content {
  display: inline-block;
  margin-left: 5%;
  width: 50%;
  white-space: pre-line;
}
#resumePreview .item ol li .content span {
  margin-left: 15%;
}



